import { fetchReferrals } from '@/src/api/referrals';
import {
  Avatar,
  Center,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  HStack,
  Spinner,
  Stack,
  Text,
  UseDisclosureReturn
} from '@chakra-ui/react';
import { useQuery } from '@tanstack/react-query';
import { useSearchParams } from 'next/navigation';
import { VerifiedAccountIcon } from '../assets/referrals';

export const ReferralsDrawer = ({
  disclosure
}: {
  disclosure: UseDisclosureReturn;
}) => {
  const searchParams = useSearchParams();

  const params = `?${searchParams?.toString()}`;

  const { data: referralsData, isLoading } = useQuery({
    queryKey: ['referrals'],
    queryFn: () => fetchReferrals(params)
  });

  const referrals = referralsData?.data?.data;

  console.log({ referrals });

  return (
    <Drawer isOpen={disclosure?.isOpen} onClose={disclosure?.onClose}>
      <DrawerOverlay />
      <DrawerContent maxW={`500px`}>
        <DrawerHeader p={`41px 24px 17px`}>
          <HStack justify={`space-between`}>
            <Text fontWeight={`600`} fontSize={`32px`} letterSpacing={`-2%`}>
              Referrals
            </Text>
            <DrawerCloseButton position={`static`} />
          </HStack>
        </DrawerHeader>
        <DrawerBody
          p={`24px`}
          sx={{
            '&::-webkit-scrollbar': {
              width: '4px', // Width of vertical scrollbar
              height: '4px' // Height of horizontal scrollbar
            },
            '&::-webkit-scrollbar-track': {
              background: 'transparent'
            },
            '&::-webkit-scrollbar-thumb': {
              background: 'gray.300', // Scrollbar color
              borderRadius: '24px'
            }
          }}
        >
          {isLoading ? (
            <Center h={`400px`}>
              <Spinner />
            </Center>
          ) : (
            <Stack gap={`40px`}>
              {referrals?.map((user: any, i: number) => (
                <HStack key={i} gap={'12px'} flex={`1`} cursor={`pointer`}>
                  <Center
                    boxSize={`48px`}
                    cursor={`pointer`}
                    position={`relative`}
                  >
                    <Avatar
                      src={user?.img || ``}
                      boxSize={`48px`}
                      name={user?.name}
                    />
                    {user?.kyc_status && <VerifiedAccountIcon />}
                  </Center>
                  <Stack
                    flex='1'
                    fontSize={'16px'}
                    color='#27272A'
                    lineHeight={'150%'}
                    fontWeight={'500'}
                    gap={'4px'}
                    textAlign={'left'}
                    letterSpacing={`0%`}
                  >
                    <Text>{user?.name}</Text>

                    <Text color={'#71717A'} fontSize={`14px`}>
                      {user?.email}, {user?.phone}
                    </Text>
                  </Stack>
                </HStack>
              ))}
            </Stack>
          )}
        </DrawerBody>
      </DrawerContent>
    </Drawer>
  );
};
