import { LayoutWrapper } from '@/src/layout/Wrapper';
import { Flex, Heading, Stack } from '@chakra-ui/react';

export const ProfilePage = () => {
  return (
    <LayoutWrapper>
      <Flex gap={`20px`}>
        <Stack w={`100%`} p={{ base: `42px 120px` }} gap={{ base: `24px` }}>
          <Heading
            fontWeight={{ base: `600` }}
            fontSize={{ base: `32px` }}
            letterSpacing={{ base: `-2%` }}
            textTransform={`capitalize`}
          >
            Profile
          </Heading>
        </Stack>
      </Flex>
    </LayoutWrapper>
  );
};
