import {
  Center,
  HStack,
  Spinner,
  Stack,
  StackDivider,
  Text,
  VStack
} from '@chakra-ui/react';
import {
  ReportTransactionIcon,
  RequestCommissionIcon
} from '../../assets/transactions/commissions';
import { SettleAccounts } from './SettlementAccounts';
import { NAVBAR_HEIGHT } from '@/src/layout/Navbar';
import { FOOTER_HEIGHT } from '@/src/layout/Footer';
import { PendingPayouts } from './PendingPayouts';
import { useQuery } from '@tanstack/react-query';
import { fetchAccountInfo } from '@/src/api/auth';
import { fetchRealtorSettingsInfo } from '@/src/api/profile';

export const CommissionSidePanel = ({}) => {
  const { data: accountInfo, isLoading } = useQuery({
    queryKey: ['account-info'],
    queryFn: fetchAccountInfo
  });
  const { data: settingsInfo, isLoading: na } = useQuery({
    queryKey: ['account-settings-info'],
    queryFn: fetchRealtorSettingsInfo
  });

  const account = accountInfo?.data;
  console.log({ account });

  return (
    <Stack
      position={`sticky`}
      top={NAVBAR_HEIGHT}
      height={`calc(100vh - ${NAVBAR_HEIGHT} - ${FOOTER_HEIGHT})`}
      w={`400px`}
      minW={`400px`}
      overflow={`auto`}
      gap={`0px`}
      borderLeft={`0.5px solid`}
      borderColor={'#E4E4E7'}
      divider={
        <StackDivider margin={`0px !important`} borderColor={'#E4E4E7'} />
      }
      sx={{
        '&::-webkit-scrollbar': {
          width: '4px', // Width of vertical scrollbar
          height: '4px' // Height of horizontal scrollbar
        },
        '&::-webkit-scrollbar-track': {
          background: 'transparent'
        },
        '&::-webkit-scrollbar-thumb': {
          background: 'gray.300', // Scrollbar color
          borderRadius: '24px'
        }
      }}
    >
      <VStack p={{ base: `46px 38px` }} gap={`6px`}>
        {isLoading ? (
          <Center height={`40px`}>
            <Spinner size={`lg`} />
          </Center>
        ) : (
          <>
            <Text
              fontWeight={'500'}
              fontSize={'14px'}
              letterSpacing={'-0.6%'}
              color={'#52525B'}
            >
              Total Commission received
            </Text>
            <Text
              fontWeight={'700'}
              fontSize={'30px'}
              letterSpacing={'-3%'}
              color={'#18181B'}
            >
              {new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD'
              }).format(account?.total_commission ?? 0)}
              {/* <Box as='span' fontSize={`14px`}>
            .00
          </Box> */}
            </Text>
          </>
        )}
      </VStack>
      <PendingPayouts />
      <SettleAccounts />
      <VStack align={`stretch`} p={`24px`} gap={`24px`}>
        <HStack
          gap={`12px`}
          fontWeight={'500'}
          fontSize={'14px'}
          lineHeight={'150%'}
          letterSpacing={'0%'}
          cursor={`pointer`}
        >
          <RequestCommissionIcon />
          <Text>Request a Commission</Text>
        </HStack>
        <HStack
          gap={`12px`}
          fontWeight={'500'}
          fontSize={'14px'}
          lineHeight={'150%'}
          letterSpacing={'0%'}
          cursor={`pointer`}
        >
          <ReportTransactionIcon />
          <Text>Report a Transaction</Text>
        </HStack>
      </VStack>
    </Stack>
  );
};
